@import url('https://fonts.googleapis.com/css2?family=Redressed&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
    background-color: #2e3440;
    display: grid;
    grid-template-rows: 15vh 80vh 5vh;
    grid-template-columns: 100px auto 100px;
}

h1 {
    text-align: center;
    color: #6fdceb ;
    font-family: Redressed ;
    font-size: 5em;
    margin: 0;
    padding: 0;
}

.title {
    grid-row: 1/2;
    grid-column: 2/-3;

}

form {
    grid-column: -3/-2;
    grid-row: 2/-3;
    background-color: #3b4252;
    border-radius: 1vh;
    padding: 1vw;
    font-family: roboto;
    display: flex;
    flex-direction: column;
    gap: 1vh;
    input {
        border: none;
        border-radius: 1vh;
        padding: 1vh;
        background-color: #4c566a;
        color: white;
        &:hover {
            box-shadow: 2px 2px 2px rgba($color: #000, $alpha: .5);
            transition: box-shadow 0.5s;
        }
        &:focus {
            border: 1px solid white;
            box-shadow: 2px 2px 2px rgba($color: #000, $alpha: .9);
            transition: box-shadow 0.5s, border 1s;
        }
    }
    .sub {
        color: white;
    }
    input[type=submit] {
        margin-top: 3vh;
    }
}



#title {
    color: white;
    font-family: Redressed;
    font-size: 3em;
    text-align: center;
}
.result {
    color: white;
    font-family: Redressed;
    font-size: 3em;
    text-align: center;
    font-style: italic;
}


#resultBut {
    @extend input ;
    text-align: center;
}

.add {
    visibility: hidden;
}

button {
    background-color: #2e3440;
    padding: 10px;
    color: white;
    border: none;
    text-align: center;
}
